.button {
    background: linear-gradient(135deg, #6e7fca, #4158d0);
    padding: 0.8rem 2rem;
    border: none;
    border-radius: 50px;
    font-size: 1.2rem;
    color: #fff;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 4px 15px rgba(65, 88, 208, 0.6);
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: 600;
}

.button:hover {
    background: linear-gradient(135deg, #4158d0, #c850c0);
    box-shadow: 0 8px 30px rgba(200, 80, 192, 0.6);
    transform: translateY(-2px);
}
