.upload {
    padding: 2rem;
  }
  
  .title {
    font-size: 1.8rem;
    margin-bottom: 1rem;
    font-weight: 500;
  }
  
  .radioGroup label {
    display: block;
    margin-bottom: 0.5rem;
  }
  
  .upload_box_btn_fixed {
    position: fixed;
    bottom: 1rem;
    right: 1rem;
    width: auto;
  }
  
  .upload_box_btn_style {
    font-size: 1.3rem;
    background-color: var(--main-bg-color);
    color: white;
    padding: 1rem 2rem;
  }

  @media screen and (max-width: 35em) {
    .upload_box_btn_fixed {
      right: 0;
      bottom: 0;
      width: 100%;
      text-align: center;
    }
  }


  .loadingIndicator {
    position: fixed; /* 使用固定定位 */
    top: 50%; /* 垂直居中 */
    left: 50%; /* 水平居中 */
    transform: translate(-50%, -50%); /* 向上和向左移动自身宽度和高度的一半 */
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 30px;
    width: 800px;
    height: 500px;
    margin: 20px 0;
    z-index: 100000; /* 确保在最上层 */
    background-color: rgba(243, 243, 243, 0.7); /* 可选：添加半透明背景 */
    padding: 20px; /* 可选：内边距 */
    border-radius: 10px; /* 可选：圆角 */
  }
  
  .spinner {
    border: 30px solid rgba(255, 255, 255, 0.3);
    border-left-color: #3e8e41; /* 选择你想要的颜色 */
    border-radius: 50%;
    width: 200px;
    height: 200px;
    animation: spin 1s linear infinite;
  }
  
  @keyframes spin {
    to {
      transform: rotate(360deg);
    }
  }
  