.wallet-options-container {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 8px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  padding: 20px;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 80%; /* 设置宽度以适应不同屏幕 */
  max-width: 400px; /* 最大宽度 */
}

  
  .wallet-button {
    margin: 20px 0; /* 按钮上下间距 */
    padding: 10px 20px; /* 按钮内边距 */
    background-color: #007bff; /* 按钮背景色 */
    color: white; /* 按钮文字颜色 */
    border: none; /* 去掉边框 */
    font-size: 30px;
    border-radius: 30px; /* 按钮圆角 */
    cursor: pointer; /* 鼠标悬停时变为手形 */
    transition: background-color 0.3s; /* 过渡效果 */
  }
  
  .wallet-button:hover {
    background-color: #0056b3; /* 鼠标悬停时按钮颜色变化 */
  }
  
  .close-button {
    position: absolute; /* 绝对定位 */
    top: 5px; /* 距离顶部10px */
    right: 10px; /* 距离右边10px */
    background: none; /* 背景透明 */
    border: none; /* 去掉边框 */
    color: #888; /* 颜色 */
    font-size: 20px; /* 字体大小 */
    cursor: pointer; /* 鼠标悬停时变为手形 */
  }
  
  .close-button:hover {
    color: #333; /* 鼠标悬停时颜色变化 */
  }
  